import React, { useState } from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import './App.css';
import Header from './components/header';
import About from './components/about';
import Projects from './components/projects';
import Contact from './components/contact';
import Footer from './components/footer';

function App() {
  const [language, setLanguage] = useState('en'); // Estado para el idioma

  return (
    <Router basename={process.env.PUBLIC_URL}>
      <div className="flex flex-col min-h-screen">
        <Header language={language} setLanguage={setLanguage} /> {/* Pasa el estado y funcin de cambio */}
        <div className="flex-grow">
          <Routes>
            <Route path="/" element={<About language={language} />} />
            <Route path="/about" element={<About language={language} />} />
            <Route path="/projects" element={<Projects language={language} />} />
            <Route path="/contact" element={<Contact language={language} />} />
          </Routes>
        </div>
        <Footer /> {/* Footer siempre estar al final */}
      </div>
    </Router>
  );
}

export default App;